
.tip_container {
    display: flex;
    flex-direction: column;
}

.tip_header {
    display: flex;
    border-bottom: 4px solid gray;
    padding-bottom: 5px;
}

.tip_title {
    flex-grow: 1;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
}

.tip_video {
    width: 320px;
    height: 205px;
    object-fit: cover;
}

.tip_close {
    font-size: 20px;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    margin-top: 3px;
    margin-right: 2px;
  }

  .close_tip:hover {
      color: red
  }

  .tip_donot_show {
    border-top: 4px solid gray;
    text-align: center;
  }
