

.footer-area {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translate(-50%, 0%);
    width: 411px;
    height: 50px;
    display: flex;
}

.pages-button {
    margin-right: 20px;
    width: 70px;
    background-color: white;
    filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
}

.undo-redo-buttons {
    display: flex;
    margin-right: 20px;
    background-color: white;
    filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
}
  
.zoom-info {
    display: flex;
    background-color: white;
    filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
}

@media only screen and (max-width: 640px) {
    .footer-area {
        top: 7px;
        left: 50%;
        transform: translate(-50%, 0%);
        height: 38px;
        width: 100px;
    }
    .zoom-info {
        display: none;
    }
    .pages-button {
        display: none;
    }
}