.page-list-container {
    width: 250px;
    height: 300px;
    border: 2px solid #575757;
    background-color: #ffffff;
}

.pages-list {
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    height: 245px;
}

.pages-list-row {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 2.5rem;
    overflow-y: hidden;
}

.pages-list-page-option {
    width: 30px;
}

.pages-list-page-name {
    width: 200px;
}