.libCell {
    height: auto;
    width: 42px;
    display: flex;
    flex-direction: column;
    margin-bottom: 13px;
    margin-top: 5px;
    margin-left: 2px;
    margin-right: 2px;
  }
  
  .libCell > span{
    font-size: xx-small;
    text-align: center;
    height: auto;
    width: 42px;
    word-wrap: break-word;
  }
  
  .libCellLoadingSpan {
    font-size: x-small;
    margin-left: 28px;
  }
  
  .dragPanel {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1500;
  }

.shape-count {
    text-align: center;
    /* padding: 0; */
    border-top: 1px solid black;
    width: calc(100% + 4px);
    margin-left: -4px;
  }
  
  .shape-count > * {
    font-size: 20px;
  }
  
  .shapes-group-div {
    /* max-height: 450px; */
    overflow: auto;
    height: calc(100vh - 150px);
  }
  
  .shapes-group-inner {
    display: flex;
    align-items: center;
    margin-bottom: 0;
  }
  
  .group-shapes-name {
    font-weight: 500;
    font-size: 16px;
    margin: 10px 0;
  }
  .group-shapes {
    margin-top: 0;
    width: 100%;
    height: auto;
    overflow-x: hidden;
    flex-wrap: wrap;
    display: flex;
    background-color: #f7f7f7;
  }
  
  .shapes-settings {
    width: 100%;
    padding: 0 7px;
    display: flex;
    align-items: center;
  }
  
  .shapes-settings > input {
    width: 60%;
    padding: 4px 6px;
    max-width: 200px;
    margin-top: 60px;
    border-radius: 5px;
  }
  
  .shapes-settings-icon {
    margin: 60px 20px 0px 20px;
    font-size: 25px;
  }
  
  .shapes-settings-close {
    display: none;
  }
  
  @media (max-width: 640px) {
    .shapes-group-inner {
      width: 100%;
      max-height: 45px;
    }
  
    .shapes-group-div {
      height: 40vh;
      max-height: 300px;
      overflow-y: scroll;
      width: 99vw;
      padding: 0 0 0 0px;
    }
  
    .shapes-settings-icon {
      margin-left: auto;
      margin-right: 10px;
      margin-top: 0px;
    }
  
    .shapes-settings > input {
      margin-top: 5px;
    }
    .shapes-settings {
      width: 99vw;
    }
  
    .shapes-settings-close {
      display: inline-block;
      margin-right: 15px;
      font-size: 25px;
    }
  }
  
  .show-more-settings {
    width: 320px;
    height: 50vh;
    background: white;
    overflow-y: hidden;
  }
  
  .show-more-close {
    font-size: 24px;
    cursor: pointer;
  }
  
  .show-more-close:hover {
    background-color: #f2f2ff;
  }
  
  .show-more-settings-inner {
    display: block;
    height: 70%;
    overflow-y: scroll;
    padding: 10px 20px 30px;
  }
  
  .show-more-settings-inner > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .show-more-settings-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 20px;
  }
  