/* * {
  box-sizing: border-box;
} */

.inner-route-body {
  box-sizing: border-box;
}

.inner-route-body * {
  box-sizing: border-box;
}

.global-container {
  width: 90%;
  margin: 0 auto;
}

.loading-spinner {
  margin: 10px auto;
  border: 8px solid #f3f3f3; /* Light grey */
  border-top: 8px solid #cc0201; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

.loading-spinner-big {
  width: 80px;
  height: 80px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (min-width: 1024px) {
  .global-container {
    width: 1200px;
  }
}

.details-grid {
  display: grid;
  grid-template-columns: 100px 1fr;
  gap: 10px;
  align-items: center;
}

.move-grid {
  display: grid;
  grid-template-columns: 1fr 30px;
  gap: 10px;
  align-items: center;
}

.share-grid {
  display: grid;
  grid-template-columns: 1fr 50px 30px;
  gap: 10px;
  align-items: center;
}

.copy-grid {
  display: grid;
  grid-template-columns: 1fr 50px;
  gap: 10px;
  align-items: center;
}
