.shape_style_paper {
  border: 2px solid #575757;
  background-color: #ffffff;
  padding: 10px;
  border-radius: 5px;
  width: 200px;
  height: 300px;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.shape_style_tabs {
  display: flex;
}

.shape_style_tab {
  flex-grow: 1;
  text-align: center;
}

.shape_style_tab_active {
  border-bottom: 4px solid #4171ba;
}

.shape_style_tab_span {
  cursor: pointer;
  color: #707070;
}

.shape_style_tab_span_active {
  cursor: pointer;
}

.shape_style_line {
  display: flex;
  flex-direction: column;
}

.shape_style_line_width {
  display: flex;
  align-items: center;
  padding-top: 4px;
}

.shape_style_line_width_text {
  flex-grow: 1;
  text-align: center;
}

.shape_style_line_dash {
  display: flex;
  border-top: 1px solid #bebebe;
  border-bottom: 1px solid #bebebe;
}

.shape_style_line_dash_cell {
  flex-grow: 1;
  cursor: pointer;
}

.shape_style_line_colors {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}

.shape_style_line_colors  svg {
  padding-left: 3px;
  padding-right: 3px;
  padding-top: 8px;
  cursor: pointer;
}

.shape_style_font_align {
  display: flex;
  align-items: center;
}

.shape_style_font_align svg {
  cursor: pointer;
}

.shape_style_font_align_left {
  flex-grow: 1;
  padding-left: 15px;
}

.shape_style_font_align_center {
  flex-grow: 1;
  text-align: center;
}

.shape_style_font_align_right {
  flex-grow: 1;
  text-align: right;
  padding-right: 15px;
}

.chess {
  background:
    url('data:image/svg+xml;utf8,<svg preserveAspectRatio="none"  viewBox="0 0 10 10" xmlns="http://www.w3.org/2000/svg"><rect x="0" y="0" width="5" height="5" fill="grey" /><rect x="5" y="5" width="5" height="5" fill="grey" /><rect x="5" y="0" width="5" height="5" fill="white" /><rect x="0" y="5" width="5" height="5" fill="white" /></svg>') 
    0 0/25% 25%;
  
  border-radius: 50%;
  width: 27px;
  height: 27px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 8px;
  margin-bottom: 3px;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

/****************************************************************************/

.connection_edit_paper {
  border: 2px solid #575757;
  background-color: #ffffff;
  padding: 15px;
  border-radius: 5px;
  max-height: 100px;
  width: 200px;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.connection_edit_header {
  text-align: center;
  font-size: 18px;
  flex-grow: 1;
}

.connection_edit_options {
  margin-top: 6px;
  display: flex;
}

.connection_edit_options_cell_space {
  flex-grow: 1;
}

.connection_edit_options_cell {
  padding-left: 5px;
  padding-right: 5px;
  width: 90px;
}

/****************************************************************************/

.shape_help_paper {
    border: 2px solid #575757;
    background-color: #ffffff;
    padding: 15px;
    border-radius: 5px;
    max-height: 150px;
    max-width: 300px;
    overflow: auto;
    font-size: 16px;
  }
  
  /****************************************************************************/

  .shape_property_paper {
    border: 2px solid #575757;
    background-color: #ffffff;
    padding: 10px;
    border-radius: 5px;
    max-height: 200px;
    max-width: 300px;
    min-width: 150px;
    overflow: auto;
    font-size: 16px;
    display: flex;
    flex-direction: column;
  }
  
  .shape_property_header {
    font-size: 18px;
    display: flex;
    flex-direction: row;
    padding-bottom: 5px;
    font-weight: bold;
  }

  .enum_picker {
    display: flex;
    flex-direction: column;
  }

  .enum_picker_header {
    font-size: 18px;
    display: flex;
    flex-direction: row;
    font-weight: bold;
    padding-bottom: 8px;
    border-bottom: 1px solid #575757;
    align-items: center;
  }
  
  .shape_property_row {
    display: flex;
    flex-direction: row;
    border-top: 1px solid #575757;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  
  .shape_property_row_slider {
    display: flex;
    flex-direction: column;
  }

  .value_slider {
      width: 80%;
  }

  .shape_property_col_1 {
    display: flex;
    flex-direction: row;
    font-size: 18px;
    align-items: center;
  }
  
  .shape_property_col_separator {
    width: 20px;
  }
  
  .shape_property_col_2 {
    display: flex;
    flex-direction: row-reverse;
    flex-grow: 1;
    font-size: 14px;
  }

  .shape_property_col_2_val {
    display: flex;
    align-items: center;
  }
