  }
}

.selection_menu_expanded_paper {
  width: 125px;
  margin-left: 18px;
}

.popper_paper {
  width: 185px;
  margin-left: 18px;
  padding-left: 5px;
}

.popper_paper:after {
  content: " ";
  border-top: 15px solid transparent;
  border-right: 18px solid #ffffff;
  border-bottom: 17px solid transparent;
  position: absolute;
  bottom: 120px;
  left: 4px;
  margin-left: auto;
}

.popper_paper svg,
.smart_draw_paper svg {
  padding: 5px;
  cursor: pointer;
}

.smart_draw_paper {
  width: 12rem;
  margin: 18px;
  margin-top: 4rem;
}

.smart_draw_paper:after {
  content: " ";
  border-top: 15px solid transparent;
  border-right: 18px solid #ffffff;
  border-bottom: 17px solid transparent;
  position: absolute;
  top: 12rem;
  left: 4px;
  margin-left: auto;
}

.container {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.header-area {
  position: absolute;
  top: 10px;
  left: calc(50% - 250px);
  width: 500px;
  height: 50px;
  display: flex;
}

.top-left-menu {
  position: absolute;
  width: 150px;
  height: 50px;
  display: flex;
}

.top-center-info {
  position: absolute;
  left: 50%;
