  padding: 7px;
}

.top-right-login {
  display: none;
}

.help-link {
  text-decoration: none;
  color: #0000ee;
}

.top-right {
  position: absolute;
  top: 10px;
  right: 15px;
  display: flex;
}

.layover {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
