@media only screen and (max-width: 640px) {
  .header {
    margin: 4px 0;
  }

  .side_menus {
    padding: 0px !important;
    position: absolute !important;
    bottom: 0;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    max-width: 100% !important;
    left: 0 !important;
    margin: 0 !important;
  }

  .side-bar {
    width: 100%;
    height: 45px;
    top: unset;
    bottom: 2px;
  }

  .side-bar .sideBarMenu {
    padding: 12px 10px;
  }

  .sideBarMenu.active {
    border-bottom: 4px solid #2f3296;
    border-left: none;
  }

  .header_text > p {
    background-color: none;
    padding: 0;
  }

  .footer {
    top: 12px;
    bottom: unset !important;
    margin: 3px;
  }

  .header-area {
    display: none;
  }

  #root .HeaderMenuIcon {
    margin: 0 !important;
    height: 32px;
    width: 32px
  }

  .HeaderMenuIcon svg {
    font-size: 23px;
  }

  .HeaderMenuIcon img {
    width: 20px;
    height: 23px;
  }

  .HeaderSection > div {
    padding: 0 8px;
  }

  .counter_percent,
  .header_text p span {
    display: none !important;
  }

  #root .header_text {
    margin: 0;
    border: none;
  }

  .header_line,
  .header_text p a {
    display: block !important;
    float: right;
  }

  #root .undo_redo_section {
    margin: 0px;
  }

  .smart_draw_res svg {
    padding: 5px;
  }

  .smart_draw_res:after {
    content: " ";
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid #fff;
    bottom: 22px;
    left: 10rem;
    margin-left: auto;
  }

  .side-bar .side_menus {
    width: 100%;
    display: inline-flex;
    height: inherit;
  }

  .side-bar .menuItems {
    display: none;
  }

  #root .resButton {
    padding: 1px 8px;
  }

  .smart_draw_paper {
    width: 100%;
    margin: 0;
    left: 5px;
    bottom: 0;
  }

  .smart_draw_res {
    width: 100%;
    bottom: 0px;
    position: relative;
  }

  .poper_paper_res {
    width: calc(100% - 9px);
    bottom: 0px;
    position: relative;
    margin: 0px;
  }

  .poper_paper_res svg {
    padding: 4px;
  }

  .popper_paper:after,
  .smart_draw_res:after {
    display: none;
  }

  .top-center-info {
    display: none;
  }

  .top-right-login {
    display: block;
    margin-right: 7px;
  }
}

